    .login {
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0)0%, rgba(0, 0, 0, 1)100%), url(https://assets.nflxext.com/ffe/siteui/vlv3/a1a5d7dc-aedf-4570-b58d-693af16e1445/01c1ea38-b8f6-4642-bc7c-258b9c1d1b5e/IN-en-20211020-popsignuptwoweeks-perspective_alpha_website_large.jpg);
        position: relative;
        height: 100vh;
        width: 100%;
        background-size: cover;
        .top {
            .wrapper {
                padding: 20px 50px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .logo {
                    height: 40px;
                }
            }
        }
        .container {
            width: 100%;
            height: 100%;
            position: absolute;
            display: flex;
            align-items: center;
            top: 0;
            left: 0;
            justify-content: center;
            flex-direction: column;
            color: white;
            form {
                width: 300px;
                height: 300px;
                padding: 30px;
                border-radius: 5px;
                background-color: var(--main-color);
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                input {
                    height: 40px;
                    border-radius: 5px;
                    background-color: white;
                    color: black;
                    padding-left: 10px;
                    &::placeholder {
                        color: rgb(73, 73, 73);
                    }
                }
                button {
                    height: 40px;
                    border-radius: 5px;
                    background-color: red;
                    color: white;
                    border: none;
                    font-size: 18px;
                    font-weight: 500;
                    cursor: pointer;
                }
                span {
                    color: gray;
                    b {
                        color: white;
                    }
                }
            }
        }
    }
    
