    @media(max-width: 360px) {
        .login .container {
            contain: strict;
        }
        .login .container form {
            height: 200px;
            width: 75%;
            padding: 30px;
            border-radius: 5px;
            background-color: #0b0b0bc2;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }
        .login .container form input {
            margin: 3px;
        }
    }