@import "../../app.scss";
.featured {
    height: 90vh;
    position: relative;
    .banner--fedeBottom {
        height: 7.4rem;
        background-image: linear-gradient( 180deg, transparent, rgba(37, 37, 37, .61), #111);
    }
    .category {
        position: absolute;
        top: 15%;
        left: 50px;
        font-size: 30px;
        font-weight: 500;
        color: white;
        display: flex;
        align-items: center;
        select {
            cursor: pointer;
            background-color: var(--main-color);
            border: 1px solid white;
            color: white;
            margin-left: 20px;
        }
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        user-select: none;
    }
    .info {
        img {
            width: 70%;
            @include mobile {
                width: 70vw;
            }
        }
        margin-left: 40px;
        margin-bottom: -80px;
        width: 50%;
        position: absolute;
        left: 0%;
        bottom: 40%;
        color: white;
        display: flex;
        flex-direction: column;
        .desc {
            margin: 10px 0px;
            @include mobile {
                width: 60vw;
            }
        }
        .button {
            display: flex;
            button {
                padding: 10px 20px;
                border: none;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: 500;
                margin-right: 10px;
                cursor: pointer;
                &.play {
                    background-color: white;
                    color: var(--main-color);
                }
                &.more {
                    background-color: gray;
                    color: white;
                }
                span {
                    margin-left: 5px;
                }
            }
        }
    }
}

.sss {
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}