.watch {
    width: 100vw;
    height: 100vh;
    .back {
        display: flex;
        align-items: center;
        position: absolute;
        top: 10px;
        left: 10px;
        color: white;
        cursor: pointer;
        z-index: 2;
    }
    .video {
        width: 100%;
        height: 46.3vw;
        object-fit: cover;
    }
    // .video iframe {
    //     width: 100%;
    //     height: 46.3vw;
    //     object-fit: cover;
    // }
}

.controls {
    width: 100%;
    height: 8.0971659919028340080971659919028%;
    /* of figure's height */
    position: relative;
    color: white;
}