.register {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0)0%, rgba(0, 0, 0, 1)100%), url(https://assets.nflxext.com/ffe/siteui/vlv3/a1a5d7dc-aedf-4570-b58d-693af16e1445/01c1ea38-b8f6-4642-bc7c-258b9c1d1b5e/IN-en-20211020-popsignuptwoweeks-perspective_alpha_website_large.jpg);
    position: relative;
    height: 100vh;
    width: 100%;
    background-size: cover;
    .top {
        .wrapper {
            padding: 20px 50px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .logo {
                height: 40px;
                cursor: pointer;
            }
            .ccc {
                background-color: Red;
                border: none;
                color: white;
                border-radius: 5px;
                padding: 5px 15px;
                font-size: 16px;
                font-weight: 500;
                cursor: pointer;
                z-index: 2;
            }
        }
    }
    .container {
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        align-items: center;
        top: 0;
        left: 0;
        justify-content: center;
        flex-direction: column;
        color: white;
        h1 {
            font-size: 50px;
        }
        h2 {
            margin: 20px;
        }
        p {
            font-size: 20px;
        }
        .input {
            width: 50%;
            background-color: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 20px;
            height: 50px;
            border-radius: 5px;
            input {
                height: 100%;
                border: none;
                padding: 0 10px;
                flex: 9;
            }
            .resgisterButton {
                flex: 3;
                height: 100%;
                background-color: red;
                color: white;
                cursor: pointer;
                border: none;
                font-size: 22px;
            }
        }
    }
}

@media(max-width: 940px) {
    .register .container h1 {
        align-items: center;
        font-size: 30px;
        text-align: center;
    }
    .register .container h2 {
        align-items: center;
        font-size: 20px;
        text-align: center;
    }
    .register .container p {
        align-items: center;
        font-size: 15px;
        text-align: center;
    }
    .register .container .input {
        width: 70%;
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20 px;
        height: 50 px;
        border-radius: 5 px;
    }
}

