@import "../../app.scss";
.navbar {
    color: white;
    font-size: 14px;
    position: fixed;
    width: 100%;
    z-index: 999;
    background: linear-gradient(to top, transparent 0%, rgb(0, 0, 0, 0.3)50%);
    transition: 0.5s;
    &.scrolled {
        background-color: var(--main-color);
    }
    .container {
        padding: 0px 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;
        .left {
            display: flex;
            align-items: center;
            img {
                height: 60px;
                margin-right: 40px;
                @include mobile {
                    display: none;
                }
            }
            span {
                margin-right: 20px;
                cursor: pointer;
                @include tablet {
                    display: none;
                }
                &.navbarnainLinks {
                    @include tablet {
                        display: block;
                    }
                }
            }
        }
        .right {
            display: flex;
            align-items: center;
            .icon {
                margin: 0px 6px;
                cursor: pointer;
            }
            img {
                width: 30px;
                height: 30px;
                border-radius: 5px;
                object-fit: cover;
                cursor: pointer;
                margin-left: 10px;
            }
            .profile {
                .options {
                    display: none;
                    background-color: var(--main-color);
                    border-radius: 5px;
                }
                span {
                    padding: 10px;
                    cursor: pointer;
                }
                &:hover {
                    .options {
                        display: flex;
                        flex-direction: column;
                        position: absolute;
                    }
                }
            }
        }
    }
}