* {
    font-family: 'Nunito', sans-serif;
}

body {
    background-color: black;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: #a70810;
}

*::selection {
    /* background: rgb(255, 93, 87); */
    background: #a70810;
}

::-webkit-scrollbar-track {
    background: black;
}

:root {
    --main-color: #0b0b0b
}

.link {
    color: inherit;
    text-decoration: none;
}

@mixin tablet {
    @media(max-width: 768px) {
        @content;
    }
}

@mixin mobile {
    @media(max-width: 480px) {
        @content;
    }
}